import Request from "./http"
// 查看所有的安心圈
export function getAllXinanCircle() {
	return Request({
		url: '/user/moment',
		method: 'GET'
	})
}
// 发布新的安心圈动态
export function updataXinanCircle(xinanCircleObj) {
	return Request({
		url: '/user/moment',
		method: 'POST',
		data: xinanCircleObj
	})
}
// 改动安心圈动态
export function editXinanCircle(xinanCircleObj) {
	return Request({
		url: '/user/moment',
		method: 'PUT',
		data: xinanCircleObj
	})
}
// 根据id删除安心圈动态
export function deleteXinanCircle(id) {
	return Request({
		url: `/user/moment/${id}`,
		method: 'DELETE',
	})
}
// 用户对安心圈点赞操作
export function heartXinanCircle(momentId, userId) {
	return Request({
		url: '/user/moment/likes',
		method: 'PUT',
		data: {
			momentId,
			userId
		}
	})
}
// 用户根据安心圈id撤销点赞
export function cancelHeartXinanCircle(momentId) {
	return Request({
		url: `/user/moment/likes/${momentId}`,
		method: 'DELETE',
	})
}

// 查看指定用户的安心圈(查看别人的安心圈)
export function getOtherXinanCircle(userId) {
	return Request({
		url: `/user/moment/${userId}`,
		method: 'GET',
	})
}